import React from "react"
import Layout from "../components/layout"
import { graphql } from 'gatsby'

export default props => {
  const { data } = props
  const siteTitle = data.site.siteMetadata.title

  return (
    <Layout location={props.location} title={siteTitle}>
      <p>
        <a href="https://jv-bagels.netlify.app" rel="noopener noreferrer" target="_blank">Bagels</a> is a number guessing game. The player gets 10 attempts to guess a 3 digit number.None of the digits repeat and you will get hints about what numbers you may have guessed correctly. The idea for this game was taken from the book <a href="https://www.amazon.com/dp/1593277954/ref=cm_sw_em_r_mt_dp_U_Ee4mEbHST0VRT" rel="noopener noreferrer nofollow" target="_blank">Invent Your Own Computer Games With Python</a> by Al Sweigart
      </p>
      <p>
        Feed the <a href="https://jv-mole-game.netlify.app" rel="noopener noreferrer" target="_blank">moles</a>  in this game that was modified from the <a href="https://frontendmasters.com/bootcamp" rel="noopener noreferrer" target="_blank">Frontend Masters</a> bootcamp Javascript course.
      </p>
    </Layout>
  )
}

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`